














import {Component, Prop, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import {INotificationChannel} from "@chaosinsight/yogipeople-portalclient"

@Component({ components: { } })
export default class UserNotifications extends Vue {
	@Prop()
	private notificationChannel!: INotificationChannel

	private notificationsSelected: boolean[] = [this.notificationChannel.BookingCancellationEnabled, this.notificationChannel.BookingConfirmationEnabled]

	private postChannel(): void {
		this.notificationChannel.Type === 1
			? this.postConfigurationsEmail()
			: this.postConfigurationsPushNotification()
	}

	/**
	 * Post notifications channels
	 */

	private postConfigurationsEmail(): void {
		const arr = this.notificationsSelected
		Portal.client.call.account.notificationChannelConfigurationsEmail(arr[0], arr[1])
	}

	private postConfigurationsPushNotification(): void {
		const arr = this.notificationsSelected
		Portal.client.call.account.notificationChannelConfigurationsPushNotification(arr[0], arr[1])
	}
}
